@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?6s6d3k");
  src: url("../fonts/icomoon.eot?6s6d3k#iefix") format("embedded-opentype"),
    url("../fonts/icomoon.ttf?6s6d3k") format("truetype"),
    url("../fonts/icomoon.woff?6s6d3k") format("woff"),
    url("../fonts/icomoon.svg?6s6d3k#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-envelope:before {
  content: "\e900";
}
.icon-phone:before {
  content: "\e901";
}
.icon-menu:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}

