body {
  font-family: "Allerta", sans-serif;
  margin: 0;
}

table {
  width: 100%;
}

th {
  background: #273b47;
  color: white;
}

th {
  padding: 10px 20px;
  text-align: left;
}

td {
  padding: 20px;
}

table,
td,
th {
  border: 1px solid #273b47;
  border-collapse: collapse;
}

input,
textarea {
  outline: 0;
}

input:focus,
textarea:focus {
  background: lightgray;
}

.contenedor {
  max-width: 800px;
  margin: auto;
  position: relative;
}

.background {
  background-color: #466173;
  background-image: url("../images/background.png");
  background-size: cover;
}
.portada.background.is-animated {
  background-image: url("../images/team-platzi-azul.jpg");
  background-repeat: no-repeat;
  animation: background 15s linear infinite alternate;
}

@keyframes background {
  0% {
    background-size: 200%;
    background-position: center;
  }
  20% {
    background-position: right top;
  }
  40% {
    background-position: center;
    background-size: 100%;
  }
  60% {
    background-position: left top;
  }
  80% {
    background-position: left -100px;
  }
  100% {
    background-position: right -100px;
    background-size: 200%;
  }
}

.button {
  border-radius: 12px;
  border: none;
  color: white;
  padding: 7px 12px;
  cursor: pointer;
  text-decoration: none;
  font-size: 18px;
  background: #fd324e;
  background: linear-gradient(to left, #fe344e, #ff7250);
}

.titulo {
  font-family: "Montserrat", sans-serif;
  font-size: 70px;
  margin-bottom: 0;
  margin-top: 70px;
}

.titulo span {
  text-decoration: underline;
}

.title-a {
  font-size: 24px;
  margin-top: 0;
}

.title-b {
  font-size: 50px;
  margin-bottom: 20px;
}

.guitarras {
  color: #1f313c;
  margin-bottom: 60px;
}

.guitarras h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 35px;
}

.portada {
  color: white;
  padding: 20px;
  height: 500px;
}

.portada button {
  margin-bottom: 70px;
}

.menu {
  font-size: 24px;
  display: inline-block;
  position: absolute;
  right: 0;
}

.menu li {
  display: inline-block;
  margin-left: 30px;
}

.menu li:before {
  content: "∘";
  font-size: 25px;
  line-height: 20px;
  color: white;
}

.menu a {
  color: white;
  text-decoration: none;
}

.logotipo {
  display: inline-block;
  margin-left: 0;
}

.guitarra {
  margin: 10px 0 40px;
  padding: 10px 2em;
  /*overflow: hidden;*/
  border: 1px solid #1f313c;
  border-radius: 5px;

  display: flex; /*NUEVO*/
  align-items: center; /*NUEVO*/
  min-height: 250px; /*NUEVO*/
  transition: 0.3s; /*NUEVO*/
  position: relative; /*NUEVO*/
}
/*NUEVO*/
.guitarra:hover {
  /* box-shadow: x y % color; */
  box-shadow: 10px 10px 0 #ff3149;
}
/*NUEVO*/
.guitarra-name {
  font-size: 3em;
  margin: 0 0 0.2em;
  padding-bottom: 0.2em;
  border-bottom: 2px dashed #1e313e;
}
/*NUEVO*/
.guitarra:nth-of-type(even):hover {
  box-shadow: -10px 10px 0 black;
}
/*NUEVO*/
.guitarra:nth-of-type(even) {
  justify-content: flex-end;
}
/*NUEVO*/
.guitarra:nth-of-type(even) .guitarra-image {
  left: 0;
  right: initial;
  height: 300px; /*TEST*/
}
/*NUEVO*/
.guitarra-image {
  position: absolute;
  right: 0;
  bottom: 0;
}
/*NUEVO*/
.guitarra ol {
  padding: 0;
  list-style: none;
  counter-reset: features;
}
/*NUEVO*/
.guitarra li::before {
  counter-increment: features;
  content: counter(features) ". ";
}
